<template>
      <li class="catalog__item">
        <router-link :to="getLink" class="catalog__pic">
          <img class="catalog__img" :src="product.image"  :alt="product.title">
        </router-link>
        <h3 class="catalog__title">
          <a href="#">
            {{ product.title }}
          </a>
        </h3>

        <span class="catalog__price">
                  {{ numberFormat(product.price) }} ₽
                </span>

        <ul class="colors colors--black">
          <li class="colors__item" v-for="color in product.colors" :key="color.id">
            <label class="colors__label" :id="color.title + product.id">
              <input class="colors__radio sr-only" :id="color.title + product.id" type="radio" v-model="currentColor" :value="color.code" />
              <span class="colors__value" :style="{backgroundColor: color.code}"> </span>
            </label>
          </li>
        </ul>
      </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import ProductFilter from '@/components/ProductFilter';

export default {
  data() {
    return {
      ProductFilter
    };
  },
  computed: {
    getLink() {
      return `/product/${this.product.id}`
    },
  },
  methods: {
    numberFormat,
  },

  props: ['product'],
};
</script>
