<template>
  <main className="content container">
    <div className="content__top content__top--catalog">
      <h1 className="content__title">
        Каталог
      </h1>
      <span className="content__info">
        152 товара
      </span>
    </div>

    <div className="content__catalog">

      <ProductFilter v-model:price-from="filterPriceFrom"
                     v-model:price-to="filterPriceTo"
                     v-model:category-id="filterCategoryId"
                     v-model:color="filterColor"/>

      <section>

        <h2 class="product__load" v-show="productLoading"><img class="spinner" src="/img/Hourglass%20(1).gif"></h2>
        <h2 class="product__load-fail" v-show="productsLoadingFailed">Произошла ошибка при загрузке товаров<button class="btn" @click.prevent="loadProducts">Попробовать еще раз</button></h2>

        <ProductList :products="products"/>
        <BasePagination @paginate="(n) => this.page = n"
                        :page="page"
                        :count="countProducts"
                        :per-page="productsPerPage"/>
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: {
    ProductList,
    BasePagination,
    ProductFilter,
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,

      page: 1,
      productsPerPage: 6,

      productsData: null,
      filterColor: null,

      productLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url
        }
          })
          : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts: function () {
      this.productLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer)
      this.loadProductsTimer = setTimeout(() => {
        return axios
            .get(API_BASE_URL + `/api/products`,{
                params: {
                  page: this.page,
                  limit: this.productsPerPage,
                  categoryId: this.filterCategoryId,
                  minPrice: this.filterPriceFrom,
                  maxPrice: this.filterPriceTo,
                  colorId: this.filterColor,
          }
        })
            .then(response => (this.productsData = response.data))
            .catch(() => this.productsLoadingFailed = true)
            .then(() => this.productLoading = false)

      }, 2000);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    }
  },
  created() {
    this.loadProducts()
  }
};
</script>
