<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link  class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
       {{$store.state.cartProducts.length}}
      </span>
    </div>
    <section class="cart">
      <h2 v-if="formIsSending" class="product__load" v-show="submitOrder"><img class="spinner" src="/img/Hourglass%20(1).gif"></h2>
      <form class="cart__form form" action="#" method="POST" @submit.prevent="submitOrder">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model:input="formData.name" type="text" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя"></BaseFormText>
            <BaseFormText v-model:input="formData.address" type="text" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес"></BaseFormText>
            <BaseFormText v-model:input="formData.phone" type="tel" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон"></BaseFormText>
            <BaseFormText v-model:input="formData.email" type="email" :error="formError.email" title="Email" placeholder="Введи ваш Email"></BaseFormText>
            <BaseFormTextarea title="Комментарий к заказу" v-model:input="formData.comment" :error="formError.comment" placeholder="Ваши пожелания"></BaseFormTextarea>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" v-for="item in products" :key="item.productId">
            <li class="cart__order">
              <h3>{{ item.product.title }}</h3>
              <b>{{  numberFormat(item.product.price) }} ₽</b>
              <span>Артикул:  {{ item.product.id }}</span>
            </li>
          </ul>


          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{$store.state.cartProducts.length}}</b> товара на сумму <b>{{ numberFormat(totalPrice) }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>{{ formErrorMessage }}</h4>
          <p>
            Похоже произошла ошибка. Попробуйте отправить снова или перезагрузите страницу.
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextarea from '@/components/BaseFormTextarea';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import CartItem from '@/components/CartItem';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';


export default {
  components: {
    BaseFormText,
    BaseFormTextarea,
    CartItem,
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formIsSending: false,
    }
  },
  methods: {
    numberFormat,
    submitOrder() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formIsSending = true;
      axios
          .post(API_BASE_URL + `/api/orders`, {...this.formData}, {
            params: {
              userAccessKey: this.$store.state.userAccessKey,
            },
          })
          .then(response => {
            this.formIsSending = false;
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
          })
          .catch(error => {
            this.formIsSending = false;
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          })
    }
  },
  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice'}),
    amount: {
      get() {
        return this.item.amount
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
      },
    },
  },
}
</script>

